.social {
  display: flex;
  justify-content: flex-end;
  align-items: center;

  &__item {

    &:not(:last-child) {
      margin-right: 10px;
    }
  }

  &__link {
    display: block;

    & svg {
      fill: var(--light-color);
      opacity: .32;
      backdrop-filter: blur(20px);
      border-radius: 30px;
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: opacity .3s ease-in-out;

      &:hover {
        opacity: 1;
      }
    }
  }

  &__link--tg {
    width: 37px;
    height: 52px;

    @include tablet {
      width: 36px;
      height: 22px;
    }
  }

  &__link--wa{
    width: 32px;
    height: 32px;

    @include tablet {
      width: 38px;
      height: 24px;
    }
  }

  &__link--vb{
    width: 33px;
    height: 36px;

    @include tablet {
      width: 26px;
      height: 24px;
    }
  }
}
