.became {
  padding-bottom: 80px;

  @include tablet {
    padding-bottom: 50px;
  }

  &__wrapper {
    padding-bottom: 70px;

    @include tablet {
      padding-bottom: 45px;
    }
  }

  &__title {
    font-family: 'Ermilov';
    font-weight: 700;
    font-size: 48px;
    line-height: 60px;
    text-transform: uppercase;
    color: var(--light-color);
    opacity: 0.2;
    margin: 0;
    margin-bottom: 50px;

    @include tablet {
      font-size: 36px;
      line-height: 48px;
    }

    @include mobile {
      font-size: 28px;
      line-height: 40px;
      margin-bottom: 40px;
    }
  }
}

.became-slider {

  @include tablet {
    margin-bottom: 20px;
  }

  @include mobile {
    margin-bottom: 30px;
  }

  &__wrap {
    position: relative;
    width: 100%;
    height: 302px;
    margin-bottom: 75px;
    pointer-events: none;
    user-select: none;

    @include big-desktop {
      height: 245px;
      margin-bottom: 55px;
    }

    @include tablet {
      height: 230px;
    }

    @include mobile {
      height: 215px;
    }
  }

  &__before {
    position: absolute;
    left: 0;
    top: 0;
    overflow: hidden;
    z-index: 2;
    width: 50%;
    height: 100%;
  }

  &__after {
    height: 100%;
  }

  &__image {
    width: 100%;
    height: 100%;
    display: block;
    object-fit: cover;
    max-width: inherit;
  }

  &__change {
    position: absolute;
    left: 50%;
    top: -15px;
    z-index: 10;
    width: 4px;
    height: 120%;
    pointer-events: visible;
    cursor: e-resize;

    @include mobile {
      height: 125%;
    }

    &::after {
      content: "";
      position: absolute;
      right: 20px;
      bottom: 15px;
      width: 15px;
      height: 15px;
      background-color: #FFC107;
      clip-path: polygon(0 50%, 100% 100%, 100% 0);

      @include big-desktop {
        bottom: 5px;
        width: 10px;
        height: 10px;
      }
    }

    &::before {
      content: "";
      position: absolute;
      left: 20px;
      bottom: 15px;
      width: 15px;
      height: 15px;
      background-color: #FFC107;
      clip-path: polygon(100% 50%, 0 100%, 0 0);

      @include big-desktop {
        bottom: 5px;
        width: 10px;
        height: 10px;
      }
    }
  }

  &__title {
    font-family: 'Ermilov';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 34px;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: var(--light-color);
    margin: 0;
    margin-bottom: 15px;

    @include mobile {
      margin-bottom: 10px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 20px;
    line-height: 32px;
    text-transform: uppercase;
    color: var(--light-color);
    margin: 0;

    @include mobile {
      font-size: 16px;
      line-height: 30px;
    }
  }
}

.after-slider {
  padding-bottom: 50px;

  @include big-desktop {
    padding-bottom: 30px;
  }

  @include mobile {
    margin: 0 16px;
  }
}

.after-slide {
  width: 100%;
}

.after-image {
  width: 100%;
}


.became-slider__ch {
  position: absolute;
  left: 50%;
  top: 0;
  width: 64px;
  height: 100%;
  transform: translateX(-50%);

  &::before {
    content: "";
    position: absolute;
    left: 50%;
    top: 0;
    transform: translateX(-50%);
    z-index: 10;
    width: 4px;
    height: 100%;
    background-color: var(--light-color);
    border-radius: 30px;
  }
}
