.nav {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 500;
  overflow-y: auto;
  width: 100%;
  height: 717px;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  background-color: rgba(21, 21, 21, 0.85);
  box-shadow: 0px 4px 40px rgb(0 0 0 / 25%);
  backdrop-filter: blur(20px);
  border-radius: 30px;

  &__content {
    display: flex;
    justify-content: space-between;
    padding-top: 130px;
    border-bottom: 3px solid rgba(255, 255, 255, .2);
    padding-bottom: 50px;
    flex-wrap: wrap;
    position: relative;
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 35px 50px;

    @include big-desktop {
      padding: 35px 50px 92px 50px;
    }

    @include tablet {
      padding: 35px 20px 92px 20px;
    }

    @include mobile {
      flex-direction: column;
    }
  }

  &__list {
    width: 33.33%;

    @include small-tablet {
      width: 100%;
    }

    &:last-child {

      @include big-desktop {
        width: 100%;

      }
    }
  }

  &__item {
    margin-bottom: 20px;
  }

  &__link {
    font-family: 'Ermilov';
    font-weight: 700;
    font-size: 24px;
    line-height: 60px;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: var(--light-color);

    @include tablet {
      font-size: 18px;
      line-height: 38px;
    }
  }

  &.menu--active {
    visibility: visible;
    opacity: 1;
  }

  &.menu--active+.burger {
    position: absolute;
    left: 93%;

    @include tablet {
      left: inherit;
      right: 10%;
    }
  }

  &__text {
    font-family: 'Ermilov';
    font-weight: 700;
    font-size: 28px;
    line-height: 60px;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: var(--light-color);
    position: absolute;
    left: 10px;
    top: 0;

    @include big-desktop {
      left: 92px;
    }

    @include small-desktop {
      left: 20px;
    }

    @include tablet {
      font-size: 20px;
      line-height: 40px;
    }
  }
}

.nav.menu--active+.burger .header__menu-text {
  font-size: 0;
}
