.article {
  padding-top: 163px;
  padding-bottom: 75px;

  @include big-desktop {
    padding-top: 150px;
    padding-bottom: 95px;
  }

  @include tablet {
    padding-top: 120px;
    padding-bottom: 75px;
  }

  @include mobile {
    padding-top: 100px;
    padding-bottom: 50px;
  }

  &__item {
    padding: 40px 35px;
    background-color: rgba(179, 179, 179, 0.12);
    backdrop-filter: blur(10px);
    border-radius: 30px;

    @include big-desktop {
      padding: 30px 20px;
    }
  }

  &__title {
    font-family: 'Ermilov';
    font-weight: 700;
    font-size: 28px;
    line-height: 40px;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: var(--light-color);
    margin: 0;
    margin-top: 25px;
    margin-bottom: 17px;
    max-width: 300px;

    @include tablet {
      font-size: 24px;
      line-height: 38px;
      max-width: 270px;
      margin-top: 18px;
    }

    @include mobile {
      font-size: 20px;
      line-height: 38px;
      max-width: 220px;
      margin-bottom: 10px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 20px;
    line-height: 32px;
    text-transform: uppercase;
    color: var(--light-color);
    opacity: 0.8;
    margin: 0;
    margin-bottom: 25px;
    display: -webkit-box;
    -webkit-line-clamp: 7;
    -webkit-box-orient: vertical;
    overflow: hidden;

    @include tablet {
      font-size: 18px;
      line-height: 32px;
    }

    @include big-desktop {
      -webkit-line-clamp: 8;
    }

    @include tablet {
      -webkit-line-clamp: 7;
      margin-bottom: 15px;
    }

    @include mobile {
      font-size: 16px;
      line-height: 32px;
      -webkit-line-clamp: 8;
    }
  }

  &__link {
    font-weight: 400;
    font-size: 20px;
    line-height: 46px;
    text-transform: uppercase;
    color: #040404;
    background-color: var(--light-color);
    border-radius: 20px;
    width: 100%;
    display: inline-block;
    text-align: center;
    padding: 8px;
    transition: opacity .3s ease-in-out;

    @include tablet {
      font-size: 18px;
    }

    &:hover {
      opacity: .2;
    }
  }
}
