.header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  padding-top: 10px;

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
  }

  &__logo {
    width: 195px;
    height: 93px;

    @include tablet {
      width: 127px;
      height: 60px;
      margin-right: auto;
    }

    svg {
      fill: var(--light-color);
      width: 100%;
      height: 100%;
    }
  }

  &__wrapper-burger {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-left: auto;

    @include tablet {
      margin-left: 40px;
    }
  }

  &__menu-text {
    font-weight: 400;
    font-size: 24px;
    line-height: 36px;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: var(--light-color);
    margin: 0;
    margin-left: 90px;

    @include big-desktop {
      font-size: 0;
    }

    &::before {
      content: "";
      position: absolute;
      left: -20px;
      top: -27px;
      width: 81px;
      height: 81px;
      background: #292929;
      box-shadow: -4px -4px 10px rgba(0, 0, 0, 0.25), 4px 4px 10px rgba(255, 255, 255, 0.16), inset -4px -4px 10px rgba(0, 0, 0, 0.25), inset 4px 4px 10px rgba(255, 255, 255, 0.16);
      border-radius: 50%;
      z-index: -1;

      @include tablet {
        width: 60px;
        height: 60px;
        left: -17px;
        top: -20px;
      }
    }
  }

  &__right {
    display: flex;

    @include tablet {
      flex-direction: column-reverse;
      margin-left: auto;
    }

    @include mobile {
      display: none;
    }
  }

  &__social {
    margin-left: 175px;

    @include big-desktop {
      margin-left: 130px;
    }

    @include tablet {
      margin-left: auto;
    }
  }

  &__tel {
    font-family: 'Ermilov';
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 46px;
    text-transform: uppercase;
    color: var(--light-color);
    margin-left: 120px;

    @include big-desktop {
      margin-left: 80px;
    }

    @include tablet {
      font-size: 20px;
      line-height: 46px;
      margin-left: auto;
    }
  }
}
