.map {
  position: relative;

  & #map {
    height: 910px;

    @include big-desktop {
      height: 660px !important;
    }

    @include tablet {
      height: 600px !important;
    }

    @include mobile {
      height: 490px !important;
    }
  }

  &__wrapper {
    background: rgba(21, 21, 21, 0.7);
    backdrop-filter: blur(10px);
    border-radius: 30px;
    position: absolute;
    left: 50%;
    bottom: 10%;
    transform: translateX(-50%);
    max-width: 1442px;
    width: 100%;
    padding: 54px;

    @include big-desktop {
      max-width: 1180px;
      padding: 40px;
    }

    @include small-desktop {
      max-width: 950px;
    }

    @include tablet {
      max-width: 470px;
      padding: 30px;
    }

    @include small-tablet {
      max-width: 328px;
      padding: 30px 20px;
      bottom: inherit;
      top: 50%;
      transform: translate(-50%, -50%);
    }
  }

  &__title {
    font-family: 'Ermilov';
    font-weight: 700;
    font-size: 96px;
    line-height: 110px;
    text-transform: uppercase;
    color: var(--light-color);
    opacity: 0.2;
    margin: 0;
    margin-bottom: 45px;

    @include big-desktop {
      font-size: 82px;
      line-height: 94px;
    }

    @include tablet {
      font-size: 64px;
      line-height: 76px;
      margin-bottom: 30px;
    }

    @include small-tablet {
      font-size: 36px;
      line-height: 50px;
      margin-bottom: 20px;
    }
  }

  &__list {
    display: flex;
    align-items: flex-start;

    @include small-desktop {
      flex-direction: column;
    }
  }

  &__item {

    @include big-desktop {
      width: 330px;
    }

    @include small-desktop {
      width: 100%;
    }

    &:not(:last-child) {
      margin-right: 9%;
      padding-right: 20px;

      @include small-desktop {
        margin-right: 0;
        padding-right: 0;
        margin-bottom: 30px;
      }

      @include tablet {
        margin-bottom: 25px;
      }

      @include small-tablet {
        margin-bottom: 15px;
      }
    }
  }

  &__item-title {
    font-family: 'Ermilov';
    font-weight: 700;
    font-size: 28px;
    line-height: 42px;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: var(--light-color);
    margin: 0;
    margin-bottom: 15px;

    @include tablet {
      font-size: 24px;
      line-height: 38px;
      margin-bottom: 5px;
    }

    @include small-tablet {
      font-size: 20px;
      line-height: 34px;
    }
  }

  &__item-text {
    font-weight: 400;
    font-size: 24px;
    line-height: 38px;
    text-transform: uppercase;
    color: var(--light-color);
    margin: 0;

    @include tablet {
      font-size: 18px;
      line-height: 32px;
    }
  }
}
