.about {
  padding-top: 80px;
  padding-bottom: 30px;
  position: relative;

  @include big-desktop {
    padding-bottom: 0;
  }

  @include tablet {
    padding-top: 65px;
  }

  @include mobile {
    padding-top: 55px;
  }

  &__container {}

  &__title {
    font-family: 'Ermilov';
    font-weight: 700;
    font-size: 96px;
    line-height: 140px;
    text-transform: uppercase;
    color: var(--light-color);
    opacity: 0.2;
    margin: 0;
    margin-bottom: 35px;

    @include big-desktop {
      font-size: 82px;
      line-height: 94px;
      margin-bottom: 25px;
    }

    @include tablet {
      font-size: 64px;
      line-height: 76px;
    }

    @include mobile {
      line-height: 48px;
      font-size: 36px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 20px;
    line-height: 32px;
    text-transform: uppercase;
    color: var(--light-color);
    margin: 0;
    margin-left: auto;
    max-width: 1073px;
    width: 100%;

    @include big-desktop {
      max-width: 675px;
    }

    @include tablet {
      max-width: 100%;
      font-size: 16px;
      line-height: 32px;
    }

    @include mobile {
      font-size: 18px;
      line-height: 32px;
    }

    &:not(:last-child) {
      margin-bottom: 50px;

      @include big-desktop {
        margin-bottom: 33px;
      }

      @include tablet {
        margin-bottom: 24px;
      }

      @include mobile {
        margin-bottom: 20px;
      }
    }
  }

  &__image {
    position: absolute;
    left: calc(50% + -1325px);
    top: 50px;
    transform: translateX(-50%);

    @include big-desktop {
      left: calc(50% - 950px);
      top: 100px;
    }

    @include tablet {
      display: none;
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      right: -225px;
      width: 834px;
      height: 834px;
      background: var(--light-color);
      filter: blur(800px);
      z-index: -5;
    }
  }
}
