.service-about {
  margin-bottom: 60px;

  @include tablet {
    margin-bottom: 30px;
  }

  &__title {
    font-family: 'Ermilov';
    font-style: normal;
    font-weight: 700;
    font-size: 48px;
    line-height: 60px;
    text-transform: uppercase;
    color: var(--light-color);
    opacity: 0.2;
    margin: 0;
    margin-bottom: 40px;

    @include big-desktop {
      margin-bottom: 30px;
    }

    @include tablet {
      font-size: 36px;
      line-height: 48px;
    }

    @include mobile {
      font-size: 28px;
      line-height: 40px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 20px;
    line-height: 32px;
    text-transform: uppercase;
    color: var(--light-color);
    margin: 0;

    @include mobile {
      font-size: 18px;
      line-height: 32px;
    }
  }
}
