.services {
  padding-bottom: 85px;

  @include tablet {
    padding-bottom: 65px;
  }

  @include mobile {
    padding-bottom: 58px;
  }

  &__title {
    font-family: 'Ermilov';
    font-weight: 700;
    font-size: 48px;
    line-height: 60px;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: var(--light-color);
    margin: 0;
    margin-bottom: 65px;

    @include big-desktop {
      margin-bottom: 45px;
    }

    @include tablet {
      font-size: 36px;
      line-height: 48px;
      margin-bottom: 30px;
    }
  }

  &__item {
    background-color: #040404;
    border-radius: 30px;
    min-height: 654px;
    position: relative;
    overflow: hidden;

    @include big-desktop {
      min-height: 535px;
    }

    @include tablet {
      min-height: 458px;
    }

    @include mobile {
      min-height: 424px;
    }

    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      right: 0;
      width: 351px;
      height: 351px;
      background-color: var(--light-color);
      box-shadow: 0px 8px 24px rgba(255, 193, 7, 0.36);
      filter: blur(200px);
      border-radius: 2px;
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      right: 0;
      top: 0;
      width: 100%;
      background-color: #040404;
      opacity: 0.6;
      transition: opacity .3s ease-in-out, background-color .3s ease-in-out;
    }

    &:hover::after {
      background-color: rgba(0, 0, 0, 0.5);
      backdrop-filter: blur(10px);
      opacity: 1;
      z-index: 10;
    }

    &:hover .services__link {
      display: inline-block;
      z-index: 100;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  &__link {
    display: none;
    font-weight: 400;
    font-size: 20px;
    line-height: 46px;
    text-transform: uppercase;
    color: #040404;
    background: var(--light-color);
    border-radius: 20px;
    padding: 8px;
    width: 85%;
    text-align: center;
    transition: opacity .3s ease-in-out, background-color .3s ease-in-out;
  }

  &__item-descr {
    position: relative;
    z-index: 1;
    padding: 55px 35px;
    display: block;

    @include big-desktop {
      padding: 50px 30px;
    }

    @include tablet {
      padding: 40px 20px;
    }
  }

  &__item-title {
    font-family: 'Ermilov';
    font-weight: 700;
    font-size: 28px;
    line-height: 42px;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: var(--light-color);
    margin: 0;
    margin-bottom: 30px;
    word-wrap: break-word;
    hyphens: auto;

    @include big-desktop {
      font-size: 24px;
      line-height: 36px;
      margin-bottom: 20px;
    }

    @include tablet {
      font-size: 20px;
      line-height: 36px;
    }
  }

  &__item-subtitle {
    font-family: 'Rubik';
    font-weight: 400;
    font-size: 24px;
    line-height: 46px;
    text-transform: uppercase;
    color: var(--light-color);
    display: block;
    letter-spacing: 0;

    @include big-desktop {
      font-size: 20px;
    }

    @include tablet {
      font-size: 20px;
      line-height: 46px;
    }
  }

  &__item-text {
    font-weight: 400;
    font-size: 24px;
    line-height: 46px;
    text-transform: uppercase;
    color: var(--light-color);
    margin: 0;

    @include big-desktop {
      font-size: 20px;
    }

    @include tablet {
      font-size: 20px;
      line-height: 46px;
    }

    @include mobile {
      font-size: 16px;
    }
  }

  &__item-image {
    position: absolute;
    left: 0;
    bottom: 0;

    @include mobile {
      width: 100%;
    }
  }
}
