.footer {

  &__top {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    margin-bottom: 40px;
  }

  &__logo {
    grid-column: 2 span;

    @include tablet {
      width: 165px;
      height: 79px;
      grid-column: 1 / 6;
    }

    @include mobile {
      width: 127px;
      height: 60px;
    }
  }

  &__address {
    grid-column: 4 span;
    background-image: url(../img/location.svg);
    padding-left: 50px;
    background-position: left top 5px;
    background-size: 31px 31px;
    background-repeat: no-repeat;
    display: inline-flex;
    flex-direction: column;
    align-items: flex-start;

    @include big-desktop {
      grid-column: 5 / 10;
      margin-bottom: 25px;
    }

    @include tablet {
      grid-column: 7 / 13;
    }

    @include mobile {
      grid-row: 3 / 4;
      grid-column: 1 / 12;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 20px;
    line-height: 43px;
    text-transform: uppercase;
    color: var(--light-color);
    font-style: normal;
    margin: 0;

    @include mobile {
      font-size: 16px;
      line-height: 26px;
    }
  }

  &__address-link {
    font-weight: 400;
    font-style: normal;
    font-size: 16px;
    line-height: 32px;
    text-decoration-line: underline;
    text-transform: uppercase;
    color: var(--light-color);
    opacity: 0.2;
    display: inline-block;

    @include mobile {
      font-size: 14px;
      line-height: 32px;
    }
  }

  &__hours {
    grid-column: 3 span;
    background-image: url(../img/clock.svg);
    margin-left: 20px;
    padding-left: 50px;
    background-position: left top 5px;
    background-size: 31px 31px;
    background-repeat: no-repeat;

    @include big-desktop {
      grid-row: 2 / 3;
      grid-column: 5 / 10;
      margin-left: 0;
    }

    @include tablet {
      grid-column: 7 / 13;
    }

    @include mobile {
      grid-row: 4 / 5;
      grid-column: 1 / 12;
    }
  }

  &__cont {
    grid-column: 3 span;
    text-align: right;

    @include big-desktop {
      grid-column: 10 / 13;
    }

    @include tablet {
      grid-row: 2 / 3;
      grid-column: 1 / 6;
      text-align: left;
    }

    @include mobile {
      grid-row: 1 / 2;
      grid-column: 6 / 13;
      text-align: right;
      margin-bottom: 30px;
    }
  }

  &__tel {
    font-family: 'Ermilov';
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 46px;
    text-transform: uppercase;
    color: var(--light-color);
    text-align: right;
    display: inline-block;
    margin-bottom: 20px;
    transition: opacity .3s ease-in-out;

    @include tablet {
      text-align: left;
    }

    @include mobile {
      font-size: 20px;
      line-height: 32px;
      margin-bottom: 10px;
    }

    &:hover {
      opacity: .2;
    }
  }

  &__social {

    @include tablet {
      justify-content: flex-start;
    }

    @include mobile {
      justify-content: flex-end;
    }
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 10px;

    @include mobile {
      display: grid;
    }
  }

  &__bottom-link {
    font-weight: 400;
    font-size: 10px;
    line-height: 12px;
    color: var(--light-color);
    transition: color 0.3s ease-in-out;
    transition: opacity .3s ease-in-out;

    @include mobile {
      &:nth-child(1) {
        grid-row: 2 / 3;
        grid-column: 1 / 6;
      }

      &:nth-child(2) {
        grid-row: 1 / 2;
        grid-column: 5 / 8;
        margin-bottom: 10px;
      }

      &:nth-child(3) {
        grid-row: 2 / 3;
        grid-column: 7 / 13;
        text-align: right;
      }
    }

    &:hover {
      opacity: 0.7;
    }
  }

  &__bottom-text {
    vertical-align: super;
  }

  &__bottom-artix {
    fill: #FAFEFD;
    width: 20px;
    height: 22px;
    margin-left: 13px;
  }
}
