  border-radius: 30px;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 30px 33px;
  padding: 40px 30px 65px;
  position: relative;

  @include tablet {
    gap: 20px;
  }

  @include mobile {
    padding: 30px 20px 90px;
  }

