    display: flex;
    flex-direction: column-reverse;

    & .just-validate-error-label {
      color: var(--light-color)!important;
      opacity: 0.8;
      margin-bottom: 10px;
    }
  }

  &__label--name {
    grid-column: 6 span;

    @include tablet {
      grid-column: 12 span;
    }
  }

  &__label--tel {
    grid-column: 6 span;

    @include tablet {
      grid-column: 12 span;
    }
  }

  &__label--date {
    grid-column: 4 span;

    @include tablet {
      grid-column: 9 span;
    }

    @include mobile {
      grid-column: 6 span;
    }
  }

  &__label--time {
    grid-column: 2 span;

    @include tablet {
      grid-column: 3 span;
    }

    @include mobile {
      grid-column: 6 span;
    }
  }

  &__input {
    padding-left: 15px;
    width: 100%;
    height: 60px;
    background: rgba(179, 179, 179, 0.12);
    border: 1px solid rgba(255, 255, 255, 0.6);
    backdrop-filter: blur(10px);
    border-radius: 20px;
    font-weight: 400;
    font-size: 20px;
    line-height: 34px;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.6);
    cursor: pointer;

    @include mobile {
      font-size: 18px;
      line-height: 32px;
    }

    &::placeholder {
      color: rgba(255, 255, 255, 0.6);
    }

    &::-webkit-calendar-picker-indicator {
      opacity: 0;
      display: none;
    }
  }

  &__btn {
    grid-column: 6 span;
    background: #FFFFFF;
    border-radius: 20px;
    font-size: 20px;
    line-height: 34px;
    text-transform: uppercase;
    color: #040404;
    padding: 13px;
    transition: opacity .3s ease-in-out;
    align-self: flex-end;

    @include tablet {
      grid-column: 12 span;
    }

    @include mobile {
      margin-bottom: 5px;
    }

    &:hover {
      opacity: .2;
    }
  }

  &__remark {
    position: absolute;
    font-size: 12px;
    line-height: 16px;
    text-transform: uppercase;
    color: #C4C4C4;
    bottom: 35px;
    right: 60px;

    @include mobile {
      right: 0;
      text-align: center;
      padding: 0 15px;
    }
  }
}
