.make {
  padding-bottom: 100px;

  @include big-desktop {
    padding-bottom: 75px;
  }

  @include mobile {
    padding-bottom: 55px;
  }

  &__title {
    font-family: 'Ermilov';
    font-weight: 700;
    font-size: 96px;
    line-height: 110px;
    text-transform: uppercase;
    color: var(--light-color);
    opacity: 0.2;
    margin: 0;
    margin-bottom: 50px;

    @include big-desktop {
      font-size: 82px;
      line-height: 96px;
    }

    @include tablet {
      font-size: 64px;
      line-height: 76px;
      margin-bottom: 30px;
    }

    @include mobile {
      font-size: 36px;
      line-height: 50px;
      margin-bottom: 25px;
    }
  }
}
