.hero {
  padding-top: 205px;
  padding-bottom: 119px;

  @include big-desktop {
    padding-top: 180px;
    padding-bottom: 35px;
  }

  @include tablet {
    padding-top: 135px;
    padding-bottom: 10px;
  }

  @include mobile {
    padding-top: 110px;
    padding-bottom: 25px;
  }

  &__container {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      right: -225px;
      width: 834px;
      height: 834px;
      background: var(--light-color);
      filter: blur(800px);
      z-index: -5;
    }
  }

  &__title {
    font-family: 'Ermilov';
    font-style: normal;
    font-weight: 700;
    font-size: 120px;
    line-height: 140px;
    text-transform: uppercase;
    color: var(--light-color);
    opacity: 0.2;
    margin: 0;
    margin-bottom: 65px;

    @include big-desktop {
      font-size: 102px;
      line-height: 122px;
      margin-bottom: 20px;
    }

    @include medium-desktop {
      font-size: 82px;
      line-height: 94px;
    }

    @include tablet {
      font-size: 62px;
      line-height: 82px;
      margin-bottom: 0;
    }

    @include mobile {
      font-size: 40px;
      line-height: 54px;
      margin-bottom: 10px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 32px;
    line-height: 52px;
    text-transform: uppercase;
    color: var(--light-color);
    margin: 0;
  }

  &__image {
    position: absolute;
    left: 405px;
    top: -190px;
    width: 149%;
    height: 100%;
    z-index: -2;

    @include big-desktop {
      left: 498px;
      top: -108px;
      width: 115%;
    }

    @include tablet {
      left: 200px;
      top: -108px;
      width: 158%;
    }

    @include mobile {
      display: none;
    }
  }
}

.link__svg {
  cursor: pointer;
  width: 255px;
  height: 255px;
  transform-box: fill-box;
  fill: var(--light-color);
  display: inline-block;
  text-transform: uppercase;
  letter-spacing: 3px;
  word-spacing: 0.3em;
  font-weight: 400;
  font-size: 24px;
  line-height: 38px;
  text-transform: uppercase;
  color: var(--light-color);

  @include big-desktop {
    width: 215px;
    height: 215px;
  }

  @include mobile {
    width: 153px;
    height: 153px;
    display: block;
    margin: 0 auto;
  }
}

.link__svg:hover .link__arrow {
  transition: transform var(--hoverDuration) var(--easeOut);
}

.link__text {
  animation: rotate normal infinite var(--rotationDuration) linear;
  transform-origin: 50% 50%;
}

.link__svg:hover .link__text {
  animation-play-state: paused;
}

@keyframes rotate {
  to {
    transform: rotate(360deg);
  }
}
