.complex {
  padding-bottom: 85px;

  @include tablet {
    padding-bottom: 60px;
  }

  &__container {
    padding-left: max(15px, calc((100% - 1440px) / 2));

    @include big-desktop {
      padding-left: max(92px, calc((100% - 1440px) / 2));
    }

    @include small-desktop {
      padding-left: max(20px, calc((100% - 1440px) / 2));
    }

    @include mobile {
      padding: 0 16px;
    }
  }

  &__title {
    font-family: 'Ermilov';
    font-weight: 700;
    font-size: 48px;
    line-height: 60px;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: var(--light-color);
    margin: 0;
    margin-bottom: 70px;

    @include big-desktop {
      margin-bottom: 45px;
    }

    @include tablet {
      font-size: 36px;
      line-height: 48px;
      margin-bottom: 30px;
    }

    @include mobile {
      font-size: 28px;
      line-height: 40px;
      margin-bottom: 20px;
    }
  }
}

.complex-slider {
  counter-reset: slide;
  margin-bottom: 50px;

  @include mobile {
    margin-bottom: 30px;
  }
}

.complex-slide {
  background: rgba(179, 179, 179, 0.12);
  backdrop-filter: blur(10px);
  border-radius: 30px;
  position: relative;
  overflow: hidden;
  height: auto;
  display: flex;

  &::before {
    counter-increment: slide;
    content: counter(slide, decimal-leading-zero);
    color: aliceblue;
    position: absolute;
    left: -40px;
    top: -40px;
    font-family: 'Ermilov';
    font-weight: 700;
    font-size: 260px;
    line-height: 236px;
    text-transform: uppercase;
    color: var(--light-color);
    opacity: 0.04;
    height: auto;

    @include big-desktop {
      font-size: 240px;
      line-height: 217px;
    }

    @include tablet {
      font-size: 200px;
      line-height: 181px;
    }

    @include mobile {
      font-size: 180px;
      line-height: 163px;
    }
  }

  &__descr {
    padding: 195px 30px 45px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    flex-grow: 1;

    @include tablet {
      padding: 155px 20px 45px;
    }

    @include mobile {
      padding: 145px 20px 25px;
    }
  }

  &__time {
    background: rgba(179, 179, 179, 0.12);
    border-radius: 30px;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    text-align: right;
    text-transform: uppercase;
    color: var(--light-color);
    padding: 13.5px 17px 13.5px 65px;
    position: absolute;
    top: 55px;
    right: 30px;
    background-image: url(../img/time.svg);
    background-size: 31px 31px;
    background-repeat: no-repeat;
    background-position: 15px 50%;

    @include tablet {
      font-size: 16px;
      line-height: 19px;
      top: 37px;
      right: 20px;
      padding: 13.5px 10px 13.5px 55px;
    }
  }

  &__title {
    font-family: 'Ermilov';
    font-weight: 700;
    font-size: 28px;
    line-height: 60px;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: var(--light-color);
    margin: 0;
    margin-bottom: 35px;

    @include big-desktop {
      margin-bottom: 30px;
    }

    @include tablet {
      font-size: 24px;
      line-height: 38px;
      margin-bottom: 10px;
    }

    @include mobile {
      font-size: 20px;
      line-height: 38px;
      margin-bottom: 30px;
    }
  }

  &__item {
    font-weight: 400;
    font-size: 20px;
    line-height: 52px;
    text-transform: uppercase;
    color: var(--light-color);
    list-style-type: disc;
    margin-left: 30px;
    margin-bottom: 50px;

    @include big-desktop {
      margin-left: 20px;
      margin-bottom: 40px;
    }

    @include tablet {
      font-size: 18px;
      line-height: 30px;
      margin-bottom: 30px;
    }

    @include mobile {
      margin-left: 15px;
      font-size: 16px;
      line-height: 30px;
    }
  }

  &__price {
    font-family: 'Ermilov';
    font-weight: 700;
    font-size: 40px;
    line-height: 60px;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: var(--light-color);
    margin: 0;
    margin-bottom: 35px;
    margin-top: auto;

    @include big-desktop {
      margin-bottom: 20px;
    }

    @include tablet {
      font-size: 32px;
      line-height: 60px;
      margin-bottom: 30px;
    }

    @include mobile {
      margin-bottom: 20px;
    }
  }

  &__link {
    background-color: var(--light-color);
    border-radius: 20px;
    font-weight: 400;
    font-size: 20px;
    line-height: 46px;
    text-align: center;
    text-transform: uppercase;
    color: #040404;
    display: inline-block;
    width: 100%;
    padding: 7px;
    transition: opacity .3s ease-in-out;

    @include big-desktop {
      padding: 7px 0;
    }

    &:hover {
      opacity: .2;
    }
  }
}
