.service-hero {
  padding-top: 150px;

  @include tablet {
    padding-top: 125px;
  }

  @include mobile {
    padding-top: 100px;
  }

  &__container {
    position: relative;

    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      right: 0;
      top: 0;
      width: calc(100% - 30px / 2);
      background-color: #040404;
      opacity: 0.6;
      border-radius: 30px;

      @include big-desktop {
        width: calc(100% - 184px);
        left: 50%;
        transform: translateX(-50%);
      }

      @include small-desktop {
        width: calc(100% - 40px);
      }

      @include mobile {
        width: calc(100% - 32px);
      }
    }
  }

  &__wrapper {
    background-color: rgba(5, 5, 5, .5);
    position: relative;
    min-height: 350px;
    height: 100%;
    border-radius: 30px;
    overflow: hidden;
    padding: 60px;
    margin-bottom: 60px;

    @include big-desktop {
      margin-bottom: 40px;
      min-height: 305px;
      height: 100%;
      padding: 50px;
    }

    @include tablet {
      padding: 30px;
      min-height: 255px;
      height: 100%;
    }

    @include mobile {
      padding: 30px 20px;
      min-height: 190px;
      height: 100%;
      margin-bottom: 25px;
    }

    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      right: 0;
      width: 351px;
      height: 351px;
      background-color: var(--light-color);
      box-shadow: 0px 8px 24px rgba(255, 193, 7, 0.36);
      filter: blur(200px);
      border-radius: 2px;
      z-index: -1;

      @include mobile {
        top: -12%;
        right: -53%;
        width: 451px;
        height: 451px;
        filter: blur(100px);
      }
    }

    &::after {
      content: "";
      position: absolute;
      top: 0;
      right: 190px;
      background: linear-gradient(90deg, #0C0C0C 12.61%, rgba(12, 12, 12, 0) 100%);
      width: 524px;
      height: 100%;
    }

    &+.price__form {
      display: none;
      position: relative;
      z-index: 500;
      margin-bottom: 20px;
      padding-top: 45px;
      border-top: 3px solid rgba(255, 255, 255, 0.2);
      padding-top: 45px;
      margin-left: 16px;
      margin-right: 16px;
      padding-left: 0;
      padding-right: 0;
    }

    &.more+.price__form {
      display: grid;
    }

    & .more .price__item-btn {
      background: rgba(255, 255, 255, .2);
      color: rgba(255, 255, 255, .5);
    }
  }

  &__title {
    font-family: 'Ermilov';
    font-style: normal;
    font-weight: 700;
    font-size: 96px;
    line-height: 110px;
    text-transform: uppercase;
    color: var(--light-color);
    opacity: 0.2;
    margin: 0;
    position: relative;
    z-index: 5;
    margin-bottom: 75px;

    @include big-desktop {
      font-size: 82px;
      line-height: 96px;
      margin-bottom: 50px;
    }

    @include tablet {
      font-size: 64px;
      line-height: 78px;
    }

    @include mobile {
      font-size: 36px;
      line-height: 48px;
      margin-bottom: 23px;
    }
  }

  &__btn {
    font-weight: 400;
    font-size: 20px;
    line-height: 46px;
    text-transform: uppercase;
    color: #040404;
    background-color: var(--light-color);
    border-radius: 20px;
    max-width: 326px;
    width: 100%;
    position: relative;
    z-index: 15;
    padding: 7px;
    transition: opacity .3s ease-in-out;

    @include mobile {
      font-size: 18px;
      line-height: 32px;
      padding: 14px;
    }

    &:hover {
      opacity: .2;
    }
  }

  &__image {
    position: absolute;
    right: 0;
    bottom: 0;

    @include tablet {
      right: -50px;
      bottom: 5px;
      width: 435px;
    }

    @include mobile {
      display: none;
    }
  }
}
