.truck {
  padding-bottom: 210px;

  @include big-desktop {
    padding-bottom: 175px;
  }

  @include tablet {
    padding-bottom: 190px;
  }

  @include mobile {
    padding-bottom: 25px;
  }

  &__container {
    position: relative;
  }

  &__tilte {
    font-family: 'Ermilov';
    font-style: normal;
    font-weight: 700;
    font-size: 96px;
    line-height: 110px;
    text-transform: uppercase;
    color: var(--light-color);
    opacity: 0.2;
    margin: 0;
    margin-bottom: 45px;

    @include big-desktop {
      font-size: 82px;
      line-height: 94px;
      margin-bottom: 33px;
    }

    @include tablet {
      font-size: 64px;
      line-height: 76px;
    }

    @include mobile {
      font-size: 36px;
      line-height: 48px;
    }
  }

  &__descr {
    background: rgba(179, 179, 179, 0.12);
    backdrop-filter: blur(10px);
    border-radius: 30px;
    padding: 54px;
    max-width: 948px;
    width: 100%;

    @include big-desktop {
      padding: 45px;
      max-width: 780px;
    }

    @include tablet {
      max-width: 540px;
      padding: 30px;
    }

    @include mobile {
      padding: 25px 20px 45px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 20px;
    line-height: 32px;
    text-transform: uppercase;
    color: var(--light-color);
    margin: 0;
    margin-bottom: 55px;
    max-width: 400px;
    width: 100%;

    @include big-desktop {
      font-size: 18px;
      line-height: 32px;
      margin-bottom: 30px;
    }

    @include tablet {
      max-width: 100%;
    }

    @include mobile {
      margin-bottom: 22px;
    }
  }

  &__link {
    background-color: var(--light-color);
    border-radius: 20px;
    font-weight: 400;
    font-size: 20px;
    line-height: 46px;
    text-transform: uppercase;
    color: #040404;
    min-width: 325px;
    text-align: center;
    padding: 7px;
    display: inline-block;
    transition: opacity .3s ease-in-out;

    @include big-desktop {
      min-width: 280px;
    }

    @include tablet {
      min-width: 210px;
      font-size: 18px;
    }

    @include mobile {
      min-width: 100%;
    }

    &:hover {
      opacity: .2;
    }
  }

  &__image {
    position: absolute;
    top: 75px;
    right: -85px;

    @include big-desktop {
      width: 941px;
      height: 450px;
      right: 0;
    }

    @include medium-desktop {
      top: 230px;
      right: -20px;
    }

    @include small-desktop {
      width: 738px;
      height: 353px;
      right: 0;
    }

    @include tablet {
      width: 534px;
      height: 254px;
      top: 245px;
    }

    @include mobile {
      width: 360px;
      height: 172px;
      top: -30px;
      position: relative;
    }
  }
}
