.video-block {
  position: relative;
  overflow: hidden;
  border-radius: 4px;
  height: 980px;
  margin-bottom: 75px;

  @include big-desktop {
    height: 675px;
  }

  @include tablet {
    height: 390px;
  }

  @include mobile {
    height: 310px;
    margin-bottom: 60px;
  }

  &::before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 3;
    background: rgb(4, 4, 4, .6);
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }

  &--played {
    &::before {
      opacity: 0;
      visibility: hidden;
    }
  }

  &__content {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__play {
    display: flex;
    justify-content: center;
    align-items: center;

    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 10;
    width: 200px;
    height: 200px;
    transform: translate(-50%, -50%);
    transition: background-color 0.3s ease-in-out, opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

    @include big-desktop {
      width: 140px;
      height: 140px;
    }

    @include tablet {
      width: 80px;
      height: 80px;
    }

    svg {
      width: 100%;
      height: 100%;
      fill: var(--light-color);
    }

    &:focus {
      outline: 2px solid var(--color-primary);
      outline-offset: 2px;
    }

    &:hover {
      background-color: var(--color-active);
    }

    &:active {
      background-color: var(--color-active);
    }

    &--played {
      opacity: 0;
      visibility: hidden;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 20px;
    line-height: 32px;
    text-transform: uppercase;
    color: var(--light-color);
    margin: 0;
    padding-bottom: 90px;

    @include tablet {
      font-size: 18px;
      line-height: 32px;
      padding-bottom: 60px;
    }
  }
}
