.reviews {
  padding-bottom: 95px;

  @include big-desktop {
    padding-bottom: 85px;
  }

  @include tablet {
    padding-bottom: 55px;
  }

  &__container {
    padding-left: max(15px, calc((100% - 1440px) / 2));

    @include big-desktop {
      padding-left: max(92px, calc((100% - 1440px) / 2));
    }

    @include small-desktop {
      padding-left: max(20px, calc((100% - 1440px) / 2));
    }

    @include mobile {
      padding: 0 16px;
    }
  }

  &__title {
    font-family: 'Ermilov';
    font-weight: 700;
    font-size: 48px;
    line-height: 60px;
    text-transform: uppercase;
    color: var(--light-color);
    opacity: 0.2;
    margin: 0;
    margin-bottom: 45px;

    @include tablet {
      font-size: 36px;
      line-height: 48px;
      margin-bottom: 25px;
    }

    @include mobile {
      font-size: 28px;
      line-height: 40px;
      margin-bottom: 30px;
    }
  }

  &__rating {
    display: flex;
    margin-bottom: 25px;

    @include tablet {
      margin-left: 5px;
    }

    @include mobile {
      margin-left: 0;
      margin-bottom: 18px;
    }
  }

  &__rating-pic {
    position: relative;
    width: 20px;
    height: 20px;
    background-image: url(../img/star.svg);
    background-repeat: no-repeat;
    background-size: 16.54px 15.8px;

    &:not(:last-child) {
      margin-right: 12px;

      @include tablet {
        margin-right: 8px;
      }
    }

    @include tablet {
      width: 15px;
      height: 15px;
      background-size: 12.41px 11.83px;
    }
  }

  &__rating-active {
    width: 20px;
    height: 20px;
    background-image: url(../img/star-all.svg);
    background-repeat: no-repeat;
    background-size: 16.54px 15.8px;

    @include tablet {
      width: 15px;
      height: 15px;
      background-size: 12.41px 11.83px;
    }
  }
}

.reviews-slider {
  padding-bottom: 50px;

  @include mobile {
    padding-bottom: 28px;
  }
}

.reviews-slide {
  background: rgba(179, 179, 179, 0.12);
  backdrop-filter: blur(10px);
  border-radius: 30px;
  padding: 50px 30px;
  min-height: 430px;

  @include tablet {
    padding: 40px 15px;
  }

  @include mobile {
    padding: 30px 20px;
    min-height: 380px;
  }

  &__title {
    font-family: 'Ermilov';
    font-style: normal;
    font-weight: 700;
    font-size: 28px;
    line-height: 60px;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: var(--light-color);
    margin: 0;
    margin-bottom: 7px;

    @include tablet {
      font-size: 20px;
      line-height: 38px;
      margin-bottom: 25px;
    }
  }

  &__text {
    font-size: 20px;
    line-height: 32px;
    text-transform: uppercase;
    color: var(--light-color);
    opacity: 0.8;
    margin: 0;

    @include tablet {
      font-size: 18px;
      line-height: 32px;
    }

    @include mobile {
      font-size: 16px;
      line-height: 32px;
    }
  }
}
