.price {
  padding-bottom: 45px;

  &__title {
    font-family: 'Ermilov';
    font-weight: 700;
    font-size: 62px;
    line-height: 76px;
    text-transform: uppercase;
    color: var(--light-color);
    opacity: 0.2;
    margin: 0;

    @include big-desktop {
      font-size: 48px;
      line-height: 60px;
    }

    @include tablet {
      font-size: 36px;
      line-height: 48px;
    }

    @include mobile {
      font-size: 28px;
      line-height: 40px;
    }
  }

  &__list {}

  &__item {
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(12, 1fr);
    align-items: center;
    position: relative;
    padding: 50px 0;

    @include big-desktop {
      padding: 35px 0;
    }

    @include tablet {
      padding: 25px 0;
    }

    &:not(:last-child) {
      &::before {
        content: "";
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 2px;
        background: var(--light-color);
        opacity: 0.2;
        backdrop-filter: blur(10px);
        border-radius: 30px;
      }
    }

    & .price__form {
      display: none;
    }

    &.more .price__form {
      display: grid;
    }

    &.more .price__item-btn {
      background: rgba(255, 255, 255, .2);
      color: rgba(255, 255, 255, .5);
    }
  }

  &__item-title {
    font-family: 'Ermilov';
    font-weight: 700;
    font-size: 28px;
    line-height: 40px;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: var(--light-color);
    grid-column: 5 span;
    margin: 0;

    @include big-desktop {
      grid-column: 4 span;
    }

    @include tablet {
      font-size: 24px;
      line-height: 30px;
      grid-column: 6 span;
    }

    @include mobile {
      font-size: 20px;
      line-height: 40px;
      grid-column: 12 span;
    }
  }

  &__item-time {
    background: rgba(179, 179, 179, 0.12);
    border-radius: 30px;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    text-align: right;
    text-transform: uppercase;
    color: var(--light-color);
    padding: 13.5px 17px 13.5px 65px;
    background-image: url(../img/time.svg);
    background-size: 31px 31px;
    background-repeat: no-repeat;
    background-position: 15px 50%;
    grid-column: 2 span;
    width: 190px;

    @include tablet {
      grid-row: 2 / 3;
      grid-column: 1 / 6;
    }
  }

  &__item-sum {
    font-family: 'Ermilov';
    font-weight: 700;
    font-size: 40px;
    line-height: 60px;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: var(--light-color);
    margin: 0;
    grid-column: 3 span;
    text-align: center;

    @include big-desktop {
      grid-column: 4 span;
    }

    @include tablet {
      grid-column: 6 span;
      text-align: right;
    }

    @include mobile {
      grid-column: 12 span;
      text-align-last: left;
      font-size: 32px;
      line-height: 50px;
    }
  }

  &__item-btn {
    background: var(--light-color);
    border-radius: 20px;
    font-weight: 400;
    font-size: 20px;
    line-height: 46px;
    text-transform: uppercase;
    color: #040404;
    padding: 7px;
    grid-column: 2 span;
    transition: opacity .3s ease-in-out;

    @include tablet {
      grid-row: 2 / 3;
      grid-column: 9 / 13;
    }

    @include mobile {
      grid-row: 4 / 5;
      grid-column: 1 / 13;
    }

    &:hover {
      opacity: .2;
    }
  }

  &__form {
    grid-row: 2 / 3;
    grid-column: 1 / 13;
    background: none;
    backdrop-filter: blur(0);
    border-radius: 0;
    padding: 40px 0 65px;

    @include tablet {
      grid-row: 3 / 4;
      padding: 20px 0 65px;
    }

    @include mobile {
      grid-row: 5 / 6;
      padding: 20px 0 100px;
    }
  }
}
