.benefit {
  padding-bottom: 85px;

  @include big-desktop {
    padding-bottom: 75px;
  }

  @include small-desktop {
    overflow-x: auto;
    overflow-y: hidden;

    &::-webkit-scrollbar {
      width: 0;
    }
  }

  @include tablet {
    padding-bottom: 55px;
  }

  @include mobile {
    padding-bottom: 40px;
  }

  &__container {}

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 36px;

    @include small-desktop {
      width: 1160px;
    }

    @include tablet {
      width: 1020px;
      gap: 20px;
    }

    @include mobile {
      width: 915px;
      gap: 10px;
    }
  }

  &__item {
    grid-column: 4 span;
    background: rgba(179, 179, 179, 0.12);
    backdrop-filter: blur(20px);
    border-radius: 30px;
    padding: 40px;

    @include big-desktop {
      padding: 35px 25px;
    }

    @include tablet {
      padding: 27px 20px;
    }
  }

  &__title {
    font-family: 'Ermilov';
    font-weight: 700;
    font-size: 28px;
    line-height: 60px;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: var(--light-color);
    margin: 0;
    margin-bottom: 15px;

    @include big-desktop {
      margin-bottom: 7px;
    }

    @include tablet {
      font-size: 24px;
      line-height: 38px;
      margin-bottom: 20px;
    }

    @include mobile {
      font-size: 20px;
      line-height: 34px;
      margin-bottom: 10px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 24px;
    line-height: 52px;
    text-transform: uppercase;
    color: var(--light-color);
    margin: 0;

    @include tablet {
      font-size: 16px;
      line-height: 32px;
    }
  }
}
