html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 360px;
  min-height: 100%;
  font-size: 16px;
  background-color: var(--body-color);
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);

  @include big-desktop {
    padding: 0 92px;
  }

  @include small-desktop {
    padding: 0 20px;
  }

  @include mobile {
    padding: 0 16px;
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: var(--grid-gap);

  @include big-desktop {
    gap: 30px;
  }

  @include tablet {
    gap: 20px;
  }

  &__item {
    grid-column: 4 span;

    @include tablet {
      grid-column: 6 span;
    }

    @include mobile {
      grid-column: 12 span;
    }
  }
}

.slider-scrollbar,
.after-scrollbar,
.reviews-scrollbar {
  opacity: 1 !important;
  background: rgba(179, 179, 179, 0.12);
  backdrop-filter: blur(10px);
  border-radius: 30px;
  height: 10px;
  padding: 0;
  max-width: 1440px;

  @include big-desktop {
    max-width: 1182px;
  }

  @include small-desktop {
    margin: 0 20px;
  }

  @include mobile {
    margin: 0 16px;
  }

  & .swiper-scrollbar-drag {
    background: var(--light-color);
    backdrop-filter: blur(10px);
    border-radius: 30px;
    height: 10px;
  }
}

.after-scrollbar {
  @include mobile {
    margin: 0;
  }
}
